***********************\n****************************************************************************************************************\n* 1.misclenious styles\n* 2.header styles\n* 3.banner styles\n* 4.footer styles\n* 5.subscribe styles\n* 6.blog styles\n* 7.faq styles\n* 8.brand styles\n* 9.testimonials styles\n* 10.pricing styles\n* 11.features styles\n* 12.services styles\n* 13.fun fact styles\n* 14.inner banner styles\n* 15.sidebar styles\n****************************************************************************************************************\n||||||||||||||||||||||||||||            End TABLE OF CONTENT                ||||||||||||||||||||||||||||||||||||\n****************************************************************************************************************/\n\n/*\n* 1.misclenious styles\n*/\nbody {\n  font-family: \"Roboto\", sans-serif;\n  font-size: 16px;\n  line-height: 30px;\n  color: #777b92;\n}\n\na:active,\na:hover,\na:focus,\na:visited {\n  text-decoration: none;\n}\n\n.page-wrapper {\n  position: relative;\n  margin: 0 auto;\n  width: 100%;\n  min-width: 300px;\n  overflow: hidden;\n}\n\n@media (min-width: 1200px) {\n  .container {\n    max-width: 1200px;\n  }\n}\nhr.style-one {\n  margin: 0;\n  border-top: 1px solid #eaeaea;\n}\n\n.block-title h2 {\n  margin: 0;\n  color: #273167;\n  font-size: 46px;\n  font-weight: 400;\n  white-space: pre-line;\n}\n\n.preloader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: 9999;\n  background-color: #fff;\n  background-image: url(../images/preloader.gif);\n  background-repeat: no-repeat;\n  background-position: center center;\n}\n\n.scroll-to-top {\n  display: inline-block;\n  width: 45px;\n  height: 45px;\n  background: #273167;\n  border-radius: 4px;\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  z-index: 99;\n  text-align: center;\n  cursor: pointer;\n  transition: all 0.4s ease;\n}\n.scroll-to-top i {\n  font-size: 18px;\n  line-height: 45